            <h1 class="title" ref="waterfall">Waterfall 瀑布流</h1>
            <p class="des">
                瀑布流组件，快速实现瀑布流布局。
            </p>
            <p class="des">
                通过作用域插槽，瀑布流组件可将数据传回父组件，父组件使用<span class="code">slot-scope</span>可以在插槽中获取到传回的数据。
            </p>
            <h2>基础用法</h2>
            <div class="container">
                <div class="example">
                    <x-waterfall :width="150" :source="source" @scroll-bottom="scrollBottom">
                        <div slot-scope="slotProps" :class="`name-${slotProps.prop.num}`">
                            <div class="name">slot</div>
                            <div class="size">宽度{{slotProps.prop.size}}</div>
                        </div>
                    </x-waterfall>
                </div>
                <pre class="code">
<span class="tag">&lt;x-waterfall</span> 
        <span class="key">:width</span><span class="tag">=</span><span class="str">"150"</span> 
        <span class="key">:source</span><span class="tag">=</span><span class="str">"source"</span> 
        <span class="key">@scroll-bottom</span><span class="tag">=</span><span class="str">"scrollBottom"</span><span class="tag">&gt;
        &lt;div</span> <span class="key">slot-scope</span><span class="tag">=</span><span class="str">"slotProps"</span><span class="tag">&gt;
                &lt;div&gt;</span>slot<span class="tag">&lt;/div&gt;
                &lt;div&gt;</span>宽度 {{`\{\{ slotProps.prop.size \}\}`}}<span class="tag">&lt;/div&gt;
        &lt;/div&gt;
&lt;/x-waterfall&gt;</span>
</pre>
            </div>
