<script>
    import mixin from '@/mixin/mixin.js'
    export default {
        name: 'Layout',
        mixins: [mixin],
        data() {
            return {
                selected: [''],
                source: []
            };
        },
        created() {
            for (let i = 0; i < 20; i++) {
                this.source.push({ num: `${i+1}`, size: '150px' });
            }
        },
        methods: {
            scrollBottom() { this.$info({ message: '被监听目标滚动到底部了', duration: 4000 }); }
        }
    }
</script>
